<template>
  <div style="max-width: 100%;box-sizing: border-box" class="financialApplyRecord">
    <myHeader :isBack="true" title="申请记录"></myHeader>
    <div class="seat-head"></div>
    <div class="list-container">
      <RecordList @click.native="gotoDetail">
        <template v-slot:title>
          记录1
        </template>
        <template v-slot:time>
          2018-09-02
        </template>
        <template v-slot:state>
          待完成
        </template>
      </RecordList>
    </div>
  </div>
</template>

<script>
  import myHeader from '@/components/Header'
  import RecordList from "../../../components/RecordList";

  export default {
    name: 'financialApplyRecord',
    data() {
      return {

      }
    },
    components: {
      RecordList,
      myHeader
    },
    methods: {
      gotoDetail() {
        this.$router.push('/financialApplyDetail');
      }
    }
  }
</script>

<style lang="scss">
  .financialApplyRecord {
    min-height: 100vh;
    background-color: #F8F8F8;

    .head {
      background-color: #FFF;
    }

    .list-container {
      margin-top: .1rem;
    }
  }
</style>



